.links {
  stroke: rgba(146, 146, 146, 0.555);
}

.node {
  position: absolute;
  height: 10px;
  width: 80px;
}

.img {
  border-radius: 40%;
  transform: translate(-10px);
}

.tooltip {
  position: absolute;
  background-color: lightgrey;
  padding: 20px;
}

.chartContainer {
  width: 100%;
  height: 100%;
}

.tv {
  position: relative;
  width: 200px;
  height: 150px;
  margin: 20px 0;
  background: rgba(200, 200, 200);
  border-radius: 10% / 10%;
  color: white;
  text-align: center;
  text-indent: .1em;
  left: 5%;
  box-shadow: 10px 10px 5px grey;
}

.tv:before {
  content: '';
  position: relative;
  top: 10%;
  bottom: 10%;
  right: -5%;
  left: -5%;
  background: inherit;
  border-radius: 5% / 50%;
}